import gql from "graphql-tag";
export default {
  apollo: {
    movie: gql`
      query getMovie {
        movie {
          poster_path
          id
          Release_Date
          Rating
          Name
          Description
          Author
        }
      }
    `,
  },

  data() {
    return {
      movie: [],
      Name: "",
      poster_path: "",
      video_path: "",
      Release_Date: "",
      Rating: "",
      Description: "",
      Author: "",
      id: "",
      genre_id: ""
    };
  },

  methods: {
    insertMov() {
      this.$apollo.mutate({
        mutation: gql`
          mutation insertMov(
            $Name: String!
            $Author: String!
            $Release_Date: date!
            $Description: String!
            $poster_path: String!
            $video_path: String!
            $Rating: Int!
            $genre_id: Int!
          ) {
            insert_movie(
              objects: {
                Author: $Author
                Description: $Description
                Name: $Name
                Rating: $Rating
                poster_path: $poster_path
                Release_Date: $Release_Date
                video_path:  $video_path
                genre_id: $genre_id
              }
            ) {
              returning {
                Author
                Description
                Name
                Rating
                Release_Date
                poster_path
                video_path
                genre_id
              }
            }
          }
        `,
        variables: {
          Author: this.Author,
          Description: this.Description,
          Name: this.Name,
          Rating: this.Rating,
          Release_Date: this.Release_Date,
          poster_path: this.poster_path,
          genre_id: this.genre_id,
          video_path: this.video_path
        },
      });

      window.location.reload();
    },

    deleteMov() {
      this.$apollo.mutate({
        mutation: gql`
          mutation MyMutation($Name: String!) {
            delete_movie(where: { Name: { _eq: $Name } }) {
              affected_rows
            }
          }
        `,
        variables: {
          Name: this.Name,
        },
      });

      window.location.reload();
    },

    updateMovbyPK() {
      this.$apollo.mutate({
        mutation: gql`
          mutation MyMutation($Name: String!, $id: Int!) {
            update_movie_by_pk(pk_columns: { id: $id }, _set: { Name: $Name }) {
              Rating
              Release_Date
              Name
              poster_path
              id
              Description
              Author
            }
          }
        `,
        variables: {
          Name: this.Name,
          id: this.id,
        },
      });

      window.location.reload();
    },
    // insertMovie(event) {
    //   this.$apollo
    //     .mutate({
    //       mutation: gql`
    //         mutation (
    //           $Name: String
    //           $poster_path: String
    //           $Release_Date: Date
    //           $Rating: Integer
    //           $Description: String
    //           $Author: String
    //         ) {
    //           insert_movie(
    //             Name: $Name
    //             poster_path: $poster_path
    //             Release_Date: $Release_Date
    //             Rating: $Rating
    //             Description: $Description
    //             Author: $Author
    //           ) {
    //             Author
    //             Description
    //             Name
    //             Rating
    //             Release_Date
    //             poster_path
    //           }
    //         }
    //       `,
    //       variables: {
    //         Author: this.Author,
    //         Description: this.Description,
    //         Name: this.Name,
    //         Rating: this.Rating,
    //         Release_Date: this.Release_Date,
    //         poster_path: this.poster_path,
    //       },
    //     })
    //     .then((data) => {
    //       event.target.reset();
    //     });
    // },
  },
};
